.banner{
    color: white;
    object-fit: contain;
    height: 448px;
}

.banner__content{
    margin-left: 15px;
    padding-top: 140px;
    height: 190px;
}
.banner__title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}
.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
    overflow-y: scroll;
}
.banner__description::-webkit-scrollbar{
    display: none;
}
.banner_button {
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    margin-right: 1rem;
    background-color: rgba(51,51,51,0.5);
    padding-bottom: 0.5rem;
}
.banner_button:hover{
    color: #000;
    background-color: white;
    transition-property: all;
    transition-duration: 0.2s;
}
.banner_fadeBottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgb(10, 10, 8),#111);
}


@media (max-width:886px) {
    .banner__description{
        width: 10rem;
    }
    .banner__title{
        font-size: 2rem;
    }
}